import React from 'react';
import './whoweare.scss';

const WhoWeAre = React.forwardRef((props, ref) => {
  return (
    <section ref={ref} id="whoweare">
      <div className='maxWidthContainer'>
        <div className='leftContainer'>
          <div className='photoContainer'>

          </div>
        </div>
        <div className='rightContainer'>
          <div className='aboutUs'>
            <span className='productDesc'>O NAS</span>
            <h1>KIM JESTEMY?</h1>
            <p>Z pewnoci jestemy zepsoem penym pasji i zapau do
              tego co robimy. Wykorzystujc swoj wiedz i
              dowiadczenie dbamy o to, eby kada podjta przez
              Ciebie decyzja bya bezpieczna dla Twojego budetu.</p>
            <div onClick={props.scrollToOurteam}className='readMoreBtn'>Czytaj dalej</div>
          </div>
        </div>
      </div>
    </section>
  );
})

export default WhoWeAre;
