import React from 'react';
import './ourteam.scss';
import PhoneAndroidIcon from '@mui/icons-material/PhoneAndroid';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PlaceIcon from '@mui/icons-material/Place';
import AccessTimeIcon from '@mui/icons-material/AccessTime';

const OurTeam = React.forwardRef((props, ref) => {
    return (
        <section ref={ref} id="ourteam">
            
                <div className="teamContainer">
                <div className='maxWidthContainer'>
                    <div className='leftContainer'>
                        <div className='aboutUs'>
                            <div className='businessPhotofirst'></div>
                            <h1>NASZE DOSWIADCZENIE</h1>
                            <p>Rozpoczynajc wspprac poczylimy wiedz z rynku
                                kapitaowego, gdzie jeden z nas pracowa przy obrocie
                                papierami wartociowymi w jednym z najwikszych
                                domw maklerskich w Polsce, a drugi w tym samym
                                czasie dostarcza szerok gam produktw finansowych
                                do przedsibiorcw z caej Polski.</p>
                            <h1>NASZA MISJA</h1>
                            <p>Nasz misj jest poprowadzenie Ciebie przez czsto
                                skomplikowane meandry finansw, tak aby czu si
                                bezpiecznie i komfortowo.</p>
                        </div>
                    </div>
                    <div className='rightContainer'>
                        <div className='aboutUs'>
                            <span className='productDesc'>O NAS</span>
                            <h1>NASZ ZESP</h1>
                            <p>Kim jestemy? Z pewnoci jestemy zepsoem
                                penym pasji i zapau do tego co robimy.
                                Wykorzystujc swoj wiedz i dowiadczenie dbamy
                                o to, eby kada podjta przez Ciebie decyzja bya
                                bezpieczna dla Twojego budetu.</p>
                            <div className='businessPhoto'></div>
                        </div>
                    </div>

                </div>
               

            </div>
        </section>
    );
})

export default OurTeam;
