import React, { useState } from 'react';
import './workwithus.scss';
import RestoreIcon from '@mui/icons-material/Restore';
import CoffeeIcon from '@mui/icons-material/Coffee';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import WorkspacePremiumIcon from '@mui/icons-material/WorkspacePremium';


const WorkWithUS = React.forwardRef((props, ref) => {


  return (
    <section ref={ref} id="workWithUS" className="calculator">
      <div className="descriptionHelp">
        <div className='maxWidthContainer'>
          <h2>Wsppracujac z nami zyskujesz</h2>
          <div className='gainContainer'>
            <div className='gainTile'>
              <RestoreIcon className='gainIcon'></RestoreIcon>
              <span>Oszczednosc czasu i pieniedzy</span>
              <p>Porwnamy oferty wielu instytucji
                finansowych, aby mg wybra
                najkorzystniejsz z nich.</p>
            </div>
            <div className='gainTile'>
              <CoffeeIcon className='gainIcon'></CoffeeIcon>
              <span>Wygode i prostote</span>
              <p>Zaatwiamy za Ciebie wszystkie
                formalnoci i dokumenty, aby mg
                skupi si na realizacji swoich celw.</p>
            </div>
            <div className='gainTile'>
              <AccountBalanceIcon className='gainIcon'></AccountBalanceIcon>
              <span>Dostep do ofert niedostepnych
                dla klientow indywidualnych</span>
              <p>Dziki wsppracy z wieloma bankami
                i firmami leasingowymi mamy dostp
                do ofert specjalnych i promocyjnych
                niedostpnych dla klientw
                indywidualnych, zapewniajc Ci
                jeszcze korzystniejsze warunki

                finansowania.</p>
            </div>
            <div className='gainTile'>
              <WorkspacePremiumIcon className='gainIcon'></WorkspacePremiumIcon>
              <span>Fachowa pomoc i doradztwo</span>
              <p>Nasi dowiadczeni eksperci posiadaj bogat
                wiedz na temat produktw finansowych i s
                gotowi odpowiedzie na wszystkie Twoje
                pytania, doradzajc Ci najlepsze rozwizania
                dopasowane do Twoich potrzeb i moliwoci

                finansowych.</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
})

export default WorkWithUS;
