import React from 'react';
import './leasingOffer.scss';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const LeasingOffer = React.forwardRef((props, ref) => {
  const offerData = [
    { title: "Leasing operacyjny", description: "Pozwala wliczy w koszty uzyskania przychodu nawet pen rat leasingow, efektywnie obniajc tym samym podstaw opodatkowania." },
    { title: "Leasing finansowy", description: "Amortyzacja i cz odsetkowa raty stanowi koszt uzyskania przychodu, wraz z ostatni rat leasingow stajesz si wacicielem przedmiotu." },
    { title: "Leasing konsumencki", description: "W przeciwniestwie do kredytu jest duo bardziej plastycznym instrumentem finansowym, pozwala ustali zasady finansowania na Twoich warunkach." },
    { title: "Leasing bez BIK I KRD", description: "Szeroka gama firm leasingowych z ktrymi wsppracujemy pozwala nam finansowa klientw z wpisami w bazach." },
    { title: "Leasing zwrotny", description: "Potrzebujesz gotwki i jednoczenie chcesz zachowa przedmiot ktry uytkujesz na codzie? Skorzystaj z leasingu zwrotnego. " },
    { title: "Poyczk leasingowa", description: "Amortyzacja i cz odsetkowa raty stanowi koszty uzyskania przychodu, od razu stajesz si wacicielem przedmiotu." },
    { title: "Wynajem dugoterminowy", description: "Usuga w ramach ktrej korzystasz z udostpnianego samochodu w zamian za comiesiczn opat, nie martwic si przy tym o ubezpieczenie OC/AC, serwis, a nawet sezonow wymian opon." },
    { title: "Kredyt samochodowy", description: "Finansowanie o charakterze celowym z niszym oprocentowaniem ni kredyt gotwkowy, pozwala rozoy spat przedmiotu na wiele lat, dopasowujc tym samym raty do Twoich moliwoci finansowych." }
  ];

  return (
    <section ref={ref} id="leasingOffer">
      <div className='creditsOfferContainer'>
        <div className='productDescription'>
            <span className='productDesc'>Nasze uslugi</span>
            <h1>METODY FINANSOWANIA</h1>
        </div>
        <div className="creditCardsContainer">
            <div className='topRow'>
              {offerData.slice(0, 4).map((offer, index) => (
                <div key={index} className="cardsOffer">
                  <div className="inner">
                    <div className="front">
                      <CheckCircleIcon className='iconTopic' />
                      <h3>{offer.title}</h3>
                    </div>
                    <div className="back">
                      <p className="description">{offer.description}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
            <div className='bottomRow'>
              {offerData.slice(4).map((offer, index) => (
                <div key={index} className="cardsOffer">
                  <div className="inner">
                    <div className="front">
                      <CheckCircleIcon className='iconTopic' />
                      <h3>{offer.title}</h3>
                    </div>
                    <div className="back">
                      <p className="description">{offer.description}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
        </div>
      </div>
    </section>
  );
});

export default LeasingOffer;
